* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  background-color: rgb(165, 165, 165);
  color: white;
  padding: 10px;
}

.heading {
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'helvetica';
  font-weight: 100;
  font-size: 48px;
}

.display {
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.image {
  height: 400px
}

.image img {
  height: 100%;
}

.sized-image {
  height: 120px;
}

.sized-image img {
  height: 100%;
}

.row, .sized-image {
  padding-right: 10px;
}

.bkgrnd {
  margin-top: 30px;
  background-color: rgb(165, 165, 165);
  height: 170px;
  padding-top: 25px;
  display: flex;
  justify-content: center;
  align-content: center;
}